$navbar-height: 64px;

/**
 * #LUYA-CONTAINER
 * Variable for the sidebar width
 */
$luya-container__sidebar--width: 300px;
$luya-container__sidebar--liveedit-active-margin: -250px;

/**
 * #SEARCH
 * Variables for the global search (navbar)
 */
$navbar__search--position: "left";
$navbar__search--width: 80%;
$navbar__search--width-mobile: 100%;

/**
 * #FILEMANAGER
 * Different variables to modify the filemanager
 */
$filemanager--base-color: "grey";
$filemanager--highlight-color: #f2f2f2;
$filemanager__tree--width: 400px;

/**
 * #SIDEBAR
 */
$sidebar--padding: 5px;

/**
 * #FLAGS (Language nav)
 */
$flag-icon-css-path: '../../img/flags' !default;
$flag-icon-rect-path: '/4x3' !default;
$flag-icon-square-path: '/1x1' !default;

/**
/* #Folder
*/

$folder-icon-svg-path: '../../img/folder' !default;

